:root {
  --primary-color: #4a6cf7;
  --primary-dark: #2a4ac7;
  --primary-light: #6a8cff;
  --secondary-color: #2d3436;
  --secondary-light: #636e72;
  --white: #fff;
  --light-gray: #f8f9fa;
  --medium-gray: #e9ecef;
  --dark-gray: #343a40;
  --black: #212529;
  --accent-1: #00b894;
  --accent-2: #6c5ce7;
  --accent-3: #9c27b0;
  --gradient-primary: linear-gradient(135deg, #4a6cf7, #9c27b0);
  --gradient-bg: linear-gradient(135deg, #18224c, #44104d);
  --gradient-secondary: linear-gradient(135deg, #191631, #4a6cf7);
  --heading-font: "Poppins", sans-serif;
  --body-font: "Roboto", sans-serif;
  --font-normal: 1rem;
  --font-medium: 1.25rem;
  --font-large: 1.5rem;
  --font-xlarge: 2rem;
  --font-xxlarge: 3rem;
  --spacing-xs: .5rem;
  --spacing-sm: 1rem;
  --spacing-md: 2rem;
  --spacing-lg: 3rem;
  --spacing-xl: 5rem;
  --transition-fast: .3s ease;
  --transition-normal: .5s ease;
  --border-radius-sm: 4px;
  --border-radius-md: 8px;
  --border-radius-lg: 16px;
  --border-radius-round: 50%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: var(--body-font);
  color: var(--dark-gray);
  background-color: var(--white);
  line-height: 1.6;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--heading-font);
  color: var(--secondary-color);
  margin-bottom: 1rem;
  font-weight: 600;
  line-height: 1.3;
}

h1 {
  font-size: var(--font-xxlarge);
}

h2 {
  font-size: var(--font-xlarge);
}

h3 {
  font-size: var(--font-large);
}

h4 {
  font-size: var(--font-medium);
}

p {
  margin-bottom: 1rem;
}

a {
  color: var(--primary-color);
  transition: var(--transition-fast);
  text-decoration: none;
}

a:hover {
  color: var(--primary-dark);
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.section-padding {
  padding: var(--spacing-xl) 0;
}

.gray-bg {
  background-color: var(--light-gray);
}

.btn {
  border-radius: var(--border-radius-md);
  transition: var(--transition-fast);
  cursor: pointer;
  text-align: center;
  border: none;
  padding: 12px 28px;
  font-size: 16px;
  font-weight: 500;
  display: inline-block;
}

.primary-btn {
  background: var(--gradient-primary);
  color: var(--white);
  z-index: 1;
  position: relative;
  overflow: hidden;
}

.primary-btn:before {
  content: "";
  background: var(--gradient-secondary);
  z-index: -1;
  opacity: 0;
  width: 100%;
  height: 100%;
  transition: opacity .3s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
}

.primary-btn:hover {
  color: var(--white);
  transform: translateY(-3px);
  box-shadow: 0 4px 12px #00000026;
}

.primary-btn:hover:before {
  opacity: 1;
}

.secondary-btn {
  color: var(--white);
  border: 2px solid var(--primary-color);
  z-index: 1;
  background-color: #0000;
  position: relative;
  overflow: hidden;
}

.secondary-btn:before {
  content: "";
  background: var(--gradient-primary);
  z-index: -1;
  opacity: 0;
  width: 100%;
  height: 100%;
  transition: opacity .3s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
}

.secondary-btn:hover {
  color: var(--white);
  border-color: #0000;
  transform: translateY(-3px);
  box-shadow: 0 4px 12px #00000026;
}

.secondary-btn:hover:before {
  opacity: 1;
}

.section-title {
  text-align: center;
  margin-bottom: var(--spacing-lg);
}

.underline {
  background: var(--gradient-primary);
  width: 80px;
  height: 4px;
  margin: 10px auto 0;
}

header {
  z-index: 1000;
  width: 100%;
  transition: var(--transition-normal);
  background-color: #0000;
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
}

header.scrolled {
  background-color: var(--white);
  padding: 12px 0;
  box-shadow: 0 2px 10px #0000001a;
}

nav, nav .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo a {
  color: var(--white);
  font-size: 28px;
  font-weight: 700;
  font-family: var(--heading-font);
}

header.scrolled .logo a {
  color: var(--secondary-color);
}

.nav-links {
  display: flex;
}

.nav-links li {
  margin-left: 30px;
}

.nav-links li a {
  color: var(--white);
  padding: 5px 0;
  font-weight: 500;
  position: relative;
}

header.scrolled .nav-links li a {
  color: var(--secondary-color);
}

.nav-links li a:after {
  content: "";
  background: var(--gradient-primary);
  width: 0;
  height: 2px;
  transition: var(--transition-fast);
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-links li a:hover:after, .nav-links li a.active:after {
  width: 100%;
}

.hamburger {
  cursor: pointer;
  display: none;
}

.bar {
  width: 25px;
  height: 3px;
  transition: var(--transition-fast);
  background-color: var(--white);
  margin: 5px auto;
  display: block;
}

header.scrolled .bar {
  background-color: var(--secondary-color);
}

.hero {
  background: var(--gradient-bg);
  color: var(--white);
  min-height: 90vh;
  padding: 180px 0 100px;
  position: relative;
  overflow: hidden;
}

.hero:before {
  content: "";
  opacity: .15;
  z-index: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.hero-content {
  z-index: 2;
  position: relative;
}

.hero-animated-bg {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.three-d-container {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.three-d-container canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
}

.circle {
  opacity: .4;
  background: #ffffff0d;
  border-radius: 50%;
  animation: 15s ease-in-out infinite float;
  position: absolute;
}

.circle:first-child {
  width: 300px;
  height: 300px;
  animation-duration: 20s;
  animation-delay: 0s;
  top: -150px;
  left: 10%;
}

.circle:nth-child(2) {
  width: 200px;
  height: 200px;
  animation-duration: 18s;
  animation-delay: 2s;
  top: 30%;
  right: 5%;
}

.circle:nth-child(3) {
  width: 150px;
  height: 150px;
  animation-duration: 15s;
  animation-delay: 4s;
  bottom: 10%;
  left: 20%;
}

.circle:nth-child(4) {
  width: 80px;
  height: 80px;
  animation-duration: 12s;
  animation-delay: 6s;
  bottom: 20%;
  right: 20%;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0)translateX(0);
  }

  25% {
    transform: translateY(-20px)translateX(10px);
  }

  50% {
    transform: translateY(10px)translateX(20px);
  }

  75% {
    transform: translateY(15px)translateX(-10px);
  }
}

.hero-content {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.hero-content h1 {
  color: var(--white);
  margin-bottom: 1rem;
  font-size: 3.5rem;
}

.hero-content h2 {
  color: var(--white);
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  font-weight: 400;
}

.hero-content p {
  color: #ffffffe6;
  margin-bottom: 2rem;
  font-size: 1.25rem;
}

.cta-buttons {
  margin-bottom: 2rem;
}

.cta-buttons .btn {
  margin: 0 10px 10px;
}

.social-links {
  margin-top: 2rem;
}

.social-links a {
  border-radius: var(--border-radius-round);
  width: 40px;
  height: 40px;
  color: var(--white);
  transition: var(--transition-fast);
  background: #ffffff1a;
  justify-content: center;
  align-items: center;
  margin: 0 8px;
  font-size: 18px;
  display: inline-flex;
}

.social-links a:hover {
  background: var(--white);
  color: var(--primary-color);
  transform: translateY(-3px);
}

.scroll-down {
  animation: 2s infinite bounce;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}

.scroll-down a {
  color: var(--white);
  font-size: 24px;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-20px);
  }

  60% {
    transform: translateY(-10px);
  }
}

.about-content {
  flex-wrap: wrap;
  gap: 40px;
  display: flex;
}

.about-text {
  flex: 1;
  min-width: 300px;
}

.about-text p {
  margin-bottom: 1.5rem;
}

.about-details {
  margin: 2rem 0;
}

.detail-item {
  margin-bottom: 1rem;
  display: flex;
}

.detail-item strong {
  min-width: 120px;
  color: var(--secondary-color);
}

.about-services {
  flex: 1;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  min-width: 300px;
  display: grid;
}

.service-card {
  background-color: var(--white);
  border-radius: var(--border-radius-md);
  transition: var(--transition-fast);
  padding: 25px;
  box-shadow: 0 5px 15px #0000000d;
}

.service-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px #0000001a;
}

.service-icon {
  width: 60px;
  height: 60px;
  color: var(--primary-color);
  border-radius: var(--border-radius-round);
  background-color: #0066cc1a;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-size: 24px;
  display: inline-flex;
}

.service-card h3 {
  margin-bottom: 15px;
  font-size: 1.25rem;
}

.download-cv {
  align-items: center;
  display: inline-flex;
}

.download-cv i {
  margin-left: 8px;
}

.timeline {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px 0;
  position: relative;
}

.timeline:before {
  content: "";
  background: var(--primary-color);
  width: 3px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.timeline-item {
  margin-bottom: 50px;
  position: relative;
}

.timeline-dot {
  background: var(--primary-color);
  z-index: 10;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.timeline-content {
  background: var(--white);
  border-radius: var(--border-radius-md);
  width: calc(50% - 40px);
  transition: var(--transition-fast);
  padding: 30px;
  position: relative;
  box-shadow: 0 5px 15px #0000000d;
}

.timeline-item:nth-child(odd) .timeline-content {
  left: 0;
}

.timeline-item:nth-child(2n) .timeline-content {
  left: calc(50% + 40px);
}

.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #0000001a;
}

.timeline-header h3 {
  color: var(--primary-color);
  margin-bottom: 5px;
}

.timeline-header .company {
  margin-bottom: 5px;
  font-weight: 600;
  display: block;
}

.timeline-header .date, .timeline-header .location {
  color: var(--secondary-light);
  margin-bottom: 5px;
  font-size: .9rem;
  display: block;
}

.timeline-body {
  margin-top: 15px;
}

.timeline-body ul {
  padding-left: 20px;
}

.timeline-body ul li {
  margin-bottom: 8px;
  padding-left: 15px;
  position: relative;
}

.timeline-body ul li:before {
  content: "•";
  color: var(--primary-color);
  position: absolute;
  left: 0;
}

.skills-content {
  max-width: 1000px;
  margin: 0 auto;
}

.top-skills {
  margin-bottom: 50px;
}

.top-skills h3, .technical-skills h3, .certifications h3 {
  text-align: center;
  color: var(--secondary-color);
  margin-bottom: 30px;
}

.skill-cards {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
  display: grid;
}

.skill-card {
  background-color: var(--white);
  border-radius: var(--border-radius-md);
  text-align: center;
  transition: var(--transition-fast);
  padding: 25px;
  box-shadow: 0 5px 15px #0000000d;
}

.skill-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 20px #0000001a;
}

.skill-icon {
  width: 70px;
  height: 70px;
  color: var(--primary-color);
  border-radius: var(--border-radius-round);
  background-color: #0066cc1a;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-size: 28px;
  display: inline-flex;
}

.skill-card h4 {
  margin-bottom: 20px;
}

.skill-level {
  background-color: var(--medium-gray);
  border-radius: 3px;
  height: 6px;
  overflow: hidden;
}

.skill-progress {
  background-color: var(--primary-color);
  border-radius: 3px;
  height: 100%;
}

.technical-skills {
  margin-bottom: 50px;
}

.skill-grid {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 25px;
  display: grid;
}

.skill-item {
  margin-bottom: 20px;
}

.skill-name {
  margin-bottom: 8px;
  font-weight: 500;
}

.skill-bar {
  background-color: var(--medium-gray);
  border-radius: 5px;
  height: 10px;
  overflow: hidden;
}

.skill-bar .skill-progress {
  position: relative;
}

.skill-bar .skill-progress span {
  color: var(--primary-color);
  font-size: 14px;
  font-weight: 600;
  position: absolute;
  top: -25px;
  right: 0;
}

.cert-container {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  display: grid;
}

.cert-item {
  background-color: var(--white);
  border-radius: var(--border-radius-md);
  transition: var(--transition-fast);
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 5px 15px #0000000d;
}

.cert-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #0000001a;
}

.cert-icon {
  width: 50px;
  height: 50px;
  color: var(--primary-color);
  border-radius: var(--border-radius-round);
  background-color: #0066cc1a;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
  font-size: 20px;
  display: flex;
}

.cert-info h4 {
  margin-bottom: 0;
  font-size: 1rem;
}

.education-content {
  max-width: 800px;
  margin: 0 auto;
}

.education-item {
  background-color: var(--white);
  border-radius: var(--border-radius-md);
  transition: var(--transition-fast);
  margin-bottom: 40px;
  padding: 30px;
  display: flex;
  box-shadow: 0 5px 15px #0000000d;
}

.education-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #0000001a;
}

.edu-icon {
  width: 60px;
  height: 60px;
  color: var(--primary-color);
  border-radius: var(--border-radius-round);
  background-color: #0066cc1a;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  font-size: 24px;
  display: flex;
}

.edu-info h3 {
  color: var(--primary-color);
  margin-bottom: 5px;
}

.edu-school {
  margin-bottom: 5px;
  font-weight: 600;
}

.edu-degree, .edu-date {
  color: var(--secondary-light);
}

.project-filters {
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

.filter-btn {
  cursor: pointer;
  border-radius: var(--border-radius-md);
  transition: var(--transition-fast);
  color: var(--secondary-color);
  background: none;
  border: none;
  margin: 0 5px 10px;
  padding: 8px 20px;
  font-size: 16px;
}

.filter-btn.active, .filter-btn:hover {
  background-color: var(--primary-color);
  color: var(--white);
}

.projects-grid {
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 30px;
  display: grid;
}

.project-card {
  background-color: var(--white);
  border-radius: var(--border-radius-md);
  transition: var(--transition-fast);
  overflow: hidden;
  box-shadow: 0 5px 15px #0000000d;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 25px #0000001a;
}

.project-img {
  height: 200px;
  overflow: hidden;
}

.project-img-placeholder {
  background: linear-gradient(135deg, var(--primary-light), var(--accent-2));
  width: 100%;
  height: 100%;
  color: var(--white);
  justify-content: center;
  align-items: center;
  font-size: 50px;
  display: flex;
}

.project-info {
  padding: 25px;
}

.project-info h3 {
  color: var(--primary-color);
  margin-bottom: 15px;
}

.project-info p {
  margin-bottom: 20px;
}

.project-tech {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.project-tech span {
  color: var(--primary-color);
  background-color: #0066cc1a;
  border-radius: 20px;
  padding: 5px 12px;
  font-size: 14px;
}

.contact-content {
  flex-wrap: wrap;
  gap: 40px;
  display: flex;
}

.contact-info, .contact-form {
  flex: 1;
  min-width: 300px;
}

.contact-info h3, .contact-form h3 {
  border-bottom: 1px solid var(--medium-gray);
  margin-bottom: 25px;
  padding-bottom: 10px;
}

.info-item {
  margin-bottom: 25px;
  display: flex;
}

.info-icon {
  width: 50px;
  height: 50px;
  color: var(--primary-color);
  border-radius: var(--border-radius-round);
  background-color: #0066cc1a;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
  font-size: 20px;
  display: flex;
}

.info-details h4 {
  margin-bottom: 5px;
  font-size: 1.1rem;
}

.info-details p {
  margin-bottom: 0;
}

.form-group {
  margin-bottom: 20px;
}

.form-group input, .form-group textarea {
  border: 1px solid var(--medium-gray);
  border-radius: var(--border-radius-md);
  width: 100%;
  font-family: var(--body-font);
  transition: var(--transition-fast);
  padding: 12px 15px;
  font-size: 16px;
}

.form-group input:focus, .form-group textarea:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 4px #0066cc1a;
}

.submit-btn {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.submit-btn i {
  margin-left: 8px;
}

footer {
  background-color: var(--secondary-color);
  color: var(--white);
  padding: 70px 0 20px;
}

.footer-content {
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 50px;
  display: flex;
}

.footer-logo, .footer-links, .footer-social {
  margin-bottom: 30px;
}

.footer-logo h3 {
  color: var(--white);
  margin-bottom: 10px;
}

.footer-logo p {
  color: #ffffffb3;
}

.footer-links ul {
  padding: 0;
}

.footer-links ul li {
  margin-bottom: 10px;
}

.footer-links ul li a {
  color: #ffffffb3;
  transition: var(--transition-fast);
}

.footer-links ul li a:hover {
  color: var(--white);
  padding-left: 5px;
}

.footer-social {
  gap: 15px;
  display: flex;
}

.footer-social a {
  width: 40px;
  height: 40px;
  color: var(--white);
  border-radius: var(--border-radius-round);
  transition: var(--transition-fast);
  background-color: #ffffff1a;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-social a:hover {
  background-color: var(--primary-color);
  transform: translateY(-5px);
}

.footer-bottom {
  text-align: center;
  border-top: 1px solid #ffffff1a;
  padding-top: 20px;
}

.footer-bottom p {
  color: #ffffffb3;
  margin-bottom: 0;
  font-size: 14px;
}

.back-to-top {
  background-color: var(--primary-color);
  width: 50px;
  height: 50px;
  color: var(--white);
  border-radius: var(--border-radius-round);
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-fast);
  justify-content: center;
  align-items: center;
  display: flex;
